import React from 'react';
import ReactDOM from 'react-dom';
import 'normalize.css/normalize.css';
import './styles/styles.scss';
import AppRouter from './routers/AppRouter';
import configureStore from './store/configureStore';
import { addExpense } from './actions/expenses';
import { setTextFilter } from './actions/filters';
import getVisibleExpenses from './selectors/expenses';
import { Provider } from 'react-redux'; //provider allows us to share the store with each component
//Can import things that are used in multiple places in app.js
import 'react-dates/lib/css/_datepicker.css';

const store = configureStore();
console.log('test');

store.dispatch(
	addExpense({ description: 'Phone Bill', amount: 1000, createdAt: 100 })
);
store.dispatch(
	addExpense({ description: 'Water Bill', amount: 500, createdAt: 1000 })
);
store.dispatch(
	addExpense({ description: 'Mortgage Bill', amount: 10000, createdAt: 101 })
);
store.dispatch(
	addExpense({ description: 'Grocery Bill', amount: 55, createdAt: 0 })
);
store.dispatch(
	addExpense({ description: 'Something Bill', amount: 501, createdAt: 105 })
);
store.dispatch(setTextFilter(''));

const jsx = (
	<Provider store={store}>
		<AppRouter />
	</Provider>
);

ReactDOM.render(jsx, document.getElementById('appRoot'));
