import React from 'react';
import ExpenseForm from './ExpenseForm';
import { editExpense, deleteExpense } from '../actions/expenses';
import { connect } from 'react-redux';

export class EditExpense extends React.Component {
	render() {
		return (
			<div>
				Edit expense: {this.props.match.params.id}
				<ExpenseForm
					onSubmit={this.submitEdit}
					expense={this.props.expense}
				/>
				<button id="deleteExpenseBtn" onClick={this.deleteExpense}>
					Delete
				</button>
			</div>
		);
	}

	submitEdit = expense => {
		this.props.editExpense(this.props.expense.id, expense);
		this.props.history.push('/');
	};

	deleteExpense = () => {
		this.props.deleteExpense({ id: this.props.expense.id });
		this.props.history.push('/');
	};
}

const mapStateToProps = (state, props) => {
	return {
		expense: state.expenses.find(
			expense => expense.id === props.match.params.id
		)
	};
};

const mapDispatchToProps = dispatch => {
	return {
		editExpense: (id, expense) => dispatch(editExpense(id, expense)),
		deleteExpense: data => dispatch(deleteExpense(data))
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(EditExpense);
